import { makeStateKey, StateKey, TransferState } from '@angular/platform-browser';
import { Header, Footer, LinkUrl, UnavComponent } from '../models';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SessionService } from './session.service';
import { ManageHeader } from './manage-header';
import { ContentPathService } from './content-path.service';
import { Observable, forkJoin, of, throwError, from } from 'rxjs';
import { map, shareReplay, concatMap, tap } from 'rxjs/operators';


export const UNAV_TRANSFER_STATE_KEY: StateKey<
  UnavTransferState
> = makeStateKey<UnavTransferState>('UNAV_TRANSFER_STATE_KEY');

export interface UnavTransferState {
  [url: string]: Header | Footer;
}

export const UNAV_TRANSFER_STATE: UnavTransferState = {};

@Injectable({
  providedIn: 'root'
})
export class ContentService {
  constructor(
    private readonly transferState: TransferState,
    private readonly http: HttpClient,
    private readonly uss: SessionService,
    private readonly manageHeader: ManageHeader,
    private readonly cps: ContentPathService
  ) {
    transferState.onSerialize(UNAV_TRANSFER_STATE_KEY, () => {
      return UNAV_TRANSFER_STATE;
    });
  }

  public getContent(
    clientId: string,
    component: UnavComponent.ComponentTypeEnum,
    siteType: string
  ): Observable<Footer | Header | undefined> {
    return forkJoin(
      this.uss.getState(),
      this.getAuthoring(clientId, component, siteType)
    )
      .pipe(
        map(res => {
          if (component === UnavComponent.ComponentTypeEnum.FOOTER) {
            return this.filterBystate(res[1], res[0]);
          } else if (UnavComponent.ComponentTypeEnum.HEADER) {
            return this.manageHeader.getHeaderData(res[1], res[0],this.uss.userSession,siteType);
          }
        })
      )
      .pipe(shareReplay(1));
  }

  public getAuthoring(
    clientId: string,
    component: UnavComponent.ComponentTypeEnum,
    siteType: string
  ): Observable<Footer | Header> {
    return from(this.cps.resolveContentUrl(component, clientId, siteType)).pipe(
      concatMap(x => {
        if (this.transferState.hasKey(UNAV_TRANSFER_STATE_KEY)) {
          const transferState = this.transferState.get<UnavTransferState>(
            UNAV_TRANSFER_STATE_KEY,
            {}
          );

          if (transferState[x]) {
            // If our SSR transfer state cache has a JSON, emit that first to prevent a flicker during SSR transition.
            return of(transferState[x]);
          }
        }

        return this.http.get<Footer | Header>(x).pipe(
          tap(v => {
            UNAV_TRANSFER_STATE[x] = v;
          })
        );
      })
    );
  }

  public parse(json: string): Observable<Footer | Header> {
    try {
      return of(JSON.parse(json));
    } catch (error) {
      return throwError(error);
    }
  }

  public filterBystate(
    data: Footer | Header,
    filterBy: LinkUrl.CustomerTypeEnum
  ): Footer | Header {
    Object.keys(data).forEach(x => {
      if (Array.isArray(data[x]) && x === 'linkUrl') {
        data[x] = this.getLinks(data[x], filterBy);
      } else if (Array.isArray(data[x])) {
        data[x].forEach(element => {
          this.filterBystate(element, filterBy);
        });
      } else if (typeof data[x] === 'object') {
        this.filterBystate(data[x], filterBy);
      }
    });
    return data;
  }
  public getLinks(Links: Array<LinkUrl>, custype: LinkUrl.CustomerTypeEnum) {
    return Links.filter(x => (x.customerType || []).indexOf(custype) > -1);
  }
}
