import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Input,
  ViewEncapsulation,
  AfterViewInit,
  Inject,
  PLATFORM_ID,
  ChangeDetectorRef,
  Output
} from '@angular/core';
import { ContentService } from '../../services/content.service';
import { Observable } from 'rxjs';
import { Footer, UnavComponent } from '../../models';
import { isPlatformBrowser } from '@angular/common';
import { tap, filter } from 'rxjs/operators';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'tmo-digital-footer',
  templateUrl: './digital-unav-footer.component.html',
  styleUrls: ['./digital-unav-footer.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DigitalUnavFooterComponent implements OnInit, AfterViewInit {
  @Input() readonly clientId: string = 'default';
  @Input() contentData: string;
  @Input() readonly mock: string = 'false';
  @Input() readonly site: string;
  @Output() digitalUnavEvent = new EventEmitter<any>();
  public content$: Observable<Footer>;

  constructor(
    private readonly cs: ContentService,
    @Inject(PLATFORM_ID) private platformId: string,
    private ref: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.content$ = this.contentData
      ? this.cs
          .parse(this.contentData)
          .pipe(tap({ complete: () => this.ref.detectChanges() }))
      : this.cs
          .getContent(
            this.clientId,
            UnavComponent.ComponentTypeEnum.FOOTER,
            this.site
          )
          .pipe(
            tap({ complete: () => this.ref.detectChanges() }),
            filter((e): e is Footer => Boolean(e))
          );
  }
  ngAfterViewInit() {
    
  }

  triggerEvent(event:string){
    this.digitalUnavEvent.emit(event);
    console.log(event);
  }
}
