import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  Renderer2,
  ViewChild,
  ChangeDetectorRef,
  Inject,
  PLATFORM_ID,
  AfterViewInit,
  HostListener
} from '@angular/core';
import { AnalyticsService } from '../../services/analytics.service';
import { SearchBarService } from '../../services/search-bar.service';
import { isPlatformBrowser } from '@angular/common';
import { Config, UNAV_CONFIG } from '../../config';

@Component({
  selector: 'tmo-search-bar',
  templateUrl: './ntm-search-bar.component.html',
  styleUrls: ['./ntm-search-bar.component.scss']
})
export class NtmSearchBarComponent implements OnChanges, AfterViewInit {
  @Input() setInputFocus: boolean;
  @Input() isOpened: boolean;
  @Input() url: string;
  @Input() searchAuthData: any;
  public targetUrl: string;
  public model = { searchTerm: null };
  public resultsState = false;
  public invetoryInfo: Array<string> = [];
  readonly isPlatformBrowser = isPlatformBrowser(this.platformId);

  @ViewChild('search') searchElem: ElementRef;
  @ViewChild('searchInput') searchInputElem: ElementRef;
  @ViewChild('searchIcon') searchIcon: ElementRef;

  constructor(
    public renderer: Renderer2,
    public readonly analyticsService: AnalyticsService,
    public ref: ChangeDetectorRef,
    public searchBarService: SearchBarService,
    @Inject(PLATFORM_ID) private platformId: string,
    @Inject(UNAV_CONFIG) private readonly config: Config
  ) {}

  @HostListener('window:resize',['$event'])
  onResize(event) {
      if(event.target.innerWidth <= 1024) {
        this.searchInputElem.nativeElement.focus();
      }
  }

  ngOnChanges(changes) {
    if(isPlatformBrowser(this.platformId)){
    this.targetUrl = this.url ? this.url.split('?')[0] : '';
    if (!this.isOpened) {
      this.resultsState = false;
    }
    setTimeout(() => {
      if (this.setInputFocus) {
        this.searchInputElem.nativeElement.focus();
      }
    }, 300);
  }
}

  triggerClick() {
    if (this.model.searchTerm) {
    this.searchIcon.nativeElement.click();
    }
}

  onModelChange() {
    this.ref.detectChanges();
  }

  public autoSuggestion(term: string) {
    if (this.searchAuthData && this.searchAuthData.enableNewDropDown) {
      const value = {};
      value['url'] = this.searchAuthData.searchURL;
      value['maxPredictive'] = this.searchAuthData.maxPredictive;
      this.searchBarService.getResultData(term, value, (res) => {
        this.invetoryInfo = res;
        this.ref.detectChanges();
      });
    }

  }

  injectScript() {
    if (isPlatformBrowser(this.platformId)) {
      [this.config.baseUrl + '/client/assets/scripts/search.js']
        .map(url => {
          const script = document.createElement('script');
          script.src = url;
          script.type = 'text/javascript';
          script.async = true;
          script.defer = true;
          script.id = 'unavSearchApi';
          return script;
        })
        .forEach(SerachScript => {
          document.body.appendChild(SerachScript);
        });
    }
  }
  ngAfterViewInit() {
    if (this.searchAuthData && this.searchAuthData.enableNewDropDown && this.searchAuthData.enableDefaultScript) {
      this.injectScript();
    }
  }

  openResults() {
    this.resultsState = true;
    this.ref.detectChanges();
  }

  clearInput() {
    this.model.searchTerm = null;
    this.openResults();

    setTimeout(() => {
      if (this.setInputFocus) {
        this.searchInputElem.nativeElement.focus();
      }
    }, 300);
  }

  selectedSuggestion(data) {
    this.resultsState = false;
    this.model.searchTerm = data;
    this.ref.detectChanges();
    this.submit();
  }

  submit() {
    if (this.model.searchTerm) {
      /* istanbul ignore next: Only wiring with window methods */
      window.location.href = this.url
        ? this.url.replace('{querryParam}', this.model.searchTerm || '')
        : window.location.href;
      /* istanbul ignore next: Only wiring with window methods */
    }
  }

}