import { Directive, ElementRef, Input, HostListener } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { AnalyticsService } from '../services/analytics.service';

@Directive({
    selector: '[tmoDigitalAnalytics]'
})
export class AnalyticsClickDirective {
    constructor(private el: ElementRef<HTMLElement>, private analyticsService: AnalyticsService) {}
    @Input() eventData: string;
    @HostListener('click')
    onClick() {
        const data = JSON.parse(this.eventData);
        const targetUrl = this.el.nativeElement.getAttribute('href') || data.targetUrl;
        const elementData = {
            targetUrl: targetUrl,
            icid: (targetUrl && targetUrl.indexOf('icid') > -1) ?
                 new HttpParams({fromString: targetUrl.split('?')[1]}).get('icid') : ''
        };
        Object.assign(data, elementData);
        if( data && data.category && data.category !== 'Search') {
          this.analyticsService.processEvent(data);
        }
        else if( data && data.category && data.category === 'Search'&& data.searchTerm) {
          this.analyticsService.processEvent(data);
        }
    }
}



