import {
  Component,
  OnInit,
  Input,
  ViewChild,
  Renderer2,
  ElementRef,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  HostListener,
  AfterViewInit,
  ViewChildren,
  QueryList,
  Output,
  EventEmitter,
  Inject,
  PLATFORM_ID,
} from '@angular/core';
import {
  trigger,
  style,
  animate,
  transition,
  group,
  query,
  stagger,
  sequence
} from '@angular/animations';
import { Observable, fromEvent } from 'rxjs';
import { ContentService } from '../../services/content.service';
import { Header } from '../../models/header';
import { BreakpointObserver, BreakpointState } from '@angular/cdk/layout';

import { UnavComponent, UnavMenuItem } from '../../models';
import { tap, switchMap, filter, startWith } from 'rxjs/operators';
import { CartService } from '../../services/cart.service';
import { AnalyticsService } from '../../services/analytics.service';
import { isPlatformBrowser } from '@angular/common';
import { Config, UNAV_CONFIG } from '../../config';

@Component({
  selector: 'tmo-digital-header',
  templateUrl: './digital-unav-header.component.html',
  styleUrls: ['./digital-unav-header.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('centerX', [
      transition('* => open', [
        query('.navbar__item', [
          style({ opacity: 0, transform: 'translate3d(-10px, 0, 0)' }),
          stagger(
            '50ms',
            animate(
              '300ms cubic-bezier(0.25, 0.1, 0.25, 1)',
              style({ opacity: 1, transform: 'translate3d(0, 0px, 0)' })
            )
          )
        ])
      ]),
      transition('open => closed', [
        sequence([
          group([
            query('.navbar__item', [
              animate(
                '300ms cubic-bezier(0.25, 0.1, 0.25, 1)',
                style({ opacity: 0, transform: 'translate3d(-15px, 0, 0)' })
              )
            ])
          ])
        ])
      ])
    ])
  ]
})
export class DigitalUnavHeaderComponent implements OnInit, AfterViewInit {
  public universalMenuState: boolean;
  public activeSlide = 'defaultMenu';
  public sideMenuState = { mobileState: false, menuState: false };
  public activeIndex: number;
  public backButton = true;
  public isSticky: boolean;
  public navMenuOffset: number;
  public navMenuOffsetHeight: string;
  public wrapperHeight;
  public menuItemData: UnavMenuItem;
  

  constructor(
    private readonly cs: ContentService,
    private readonly cartService: CartService,
    private breakpointObserver: BreakpointObserver,
    private renderer: Renderer2,
    public analyticsService: AnalyticsService,
    private ref: ChangeDetectorRef,
    @Inject(PLATFORM_ID) private platformId: string,
    @Inject(UNAV_CONFIG) private readonly config: Config
  ) { }
  @Input() readonly theme: UnavComponent.NavThemeTypeEnum = UnavComponent.NavThemeTypeEnum.LIGHT;
  @Input() readonly skinny: UnavComponent.EnableTypeEnum = UnavComponent.EnableTypeEnum.FALSE;
  @Input() readonly sticky: UnavComponent.EnableTypeEnum = UnavComponent.EnableTypeEnum.TRUE;
  @Input() readonly clientId: string = UnavComponent.ComponentNavTypeEnum.DEFAULT;
  @Input() readonly site: string;
  @Input() contentData: string;
  @Output() digitalUnavEvent = new EventEmitter<any>();


  @ViewChild('prevSlideButton') prevSlideButton: ElementRef;
  @ViewChild('defaultMenu') defaultMenu: ElementRef;
  @ViewChild('headerNavbar') headerNavBar: ElementRef;
  @ViewChild('stickyHeader') stickyHeaderUno: ElementRef;
  @ViewChildren('stickyHeader') stickyHeader: QueryList<ElementRef>;
  public content$: Observable<Header | undefined>;
  public cartCounter$: Observable<number|undefined>;

  @HostListener('window:scroll', [])
  onWindowScroll() {
    if (this.sticky === 'true') {
      if (window.pageYOffset > this.navMenuOffset) {
        this.isSticky = true;
      } else {
        this.isSticky = false;
      }
      this.ref.detectChanges();
    }
  }
  ngAfterViewInit() {
    if (this.stickyHeaderUno && this.stickyHeaderUno.nativeElement) {
      this.navMenuOffset = this.stickyHeaderUno.nativeElement.offsetTop;
      this.navMenuOffsetHeight = this.stickyHeaderUno.nativeElement.clientHeight + 'px';
      //this.wrapperHeight = { 'height': this.navMenuOffsetHeight };
    }

    if (!this.navMenuOffset) {
      this.stickyHeader.changes.subscribe((
        item => {
          if (this.stickyHeader.length) {
            this.navMenuOffset = this.stickyHeader.first.nativeElement.offsetTop;
            this.navMenuOffsetHeight = this.stickyHeader.first.nativeElement.clientHeight + 'px';
           // this.wrapperHeight = { 'height': this.navMenuOffsetHeight };
          }
        }));
    }
    this.ref.detectChanges();

    if (isPlatformBrowser(this.platformId)) {
      [ this.config.baseUrl + '/client/assets/scripts/mp_linkcode.js', 
        this.config.baseUrl + '/client/assets/scripts/motionpoint_v2.js', 
        'https://tmobilees.mpeasylink.com/mpel/mpel.js'
      ]
        .map(url => {
          const script = document.createElement('script');
          script.src = url;
          script.type = 'text/javascript';
          script.async = true;
          script.defer = true;
          script.id = 'mpelid';
          return script;
        })
        .forEach(MpScript => {
          document.body.appendChild(MpScript);
        });
    }
  }

  ngOnInit() {
    this.breakpointObserver
      .observe(['(max-width: 1023px)'])
      .subscribe((breakPointState: BreakpointState) => {
        if (breakPointState.matches) {
          this.sideMenuState.mobileState = true;
          this.sideMenuState.menuState = false;
          this.analyticsService.navType = 'tNav';
          this.ref.detectChanges();
        } else {
          this.sideMenuState.mobileState = false;
          this.sideMenuState.menuState = false;
          this.analyticsService.navType = 'tNav';
          this.ref.detectChanges();
        }
      });

    this.content$ = this.contentData
      ? this.cs.parse(this.contentData)
        .pipe(tap({ complete: () => this.ref.detectChanges() }))
      : this.cs.getContent(this.clientId, UnavComponent.ComponentTypeEnum.HEADER, this.site
      )
        .pipe(tap({ complete: () => this.ref.detectChanges() }));

    this.cartCounter$ = fromEvent<CustomEvent>(window, 'unavBadgeEvent')
      .pipe(
        startWith({ detail: 'cart' }),
        filter(event => event.detail === 'cart'),
        switchMap(() =>
          this.cartService
            .getCartcounter()
            .pipe(tap({ complete: () => this.ref.detectChanges() }))
        ));
  }

  toggleDefaultMenu() {
    this.activeSlide = 'defaultMenu';
    if (this.sideMenuState.mobileState) {
      this.renderer.setAttribute(
        this.defaultMenu.nativeElement,
        'aria-hidden',
        'false'
      );
    }
    this.ref.detectChanges();
  }

  toggleLoginMenu(util?: boolean) {
    if (this.activeSlide === 'loginMenu') {
      this.backButton = true;
      this.activeSlide = 'defaultMenu';
      this.renderer.setAttribute(
        this.defaultMenu.nativeElement,
        'aria-hidden',
        'false'
      );
    } else {
      this.activeSlide = 'loginMenu';
      if (this.sideMenuState.mobileState) {
        util ? this.backButton = false :
          setTimeout(() => {
            this.prevSlideButton.nativeElement.focus();
          }, 0);
        this.renderer.setAttribute(
          this.defaultMenu.nativeElement,
          'aria-hidden',
          'true'
        );
      }
    }
    this.ref.detectChanges();
  }

  openHamburger() {
    this.sideMenuState.menuState = true;
    this.analyticsService.navType = 'hNav';
    if (this.sideMenuState.mobileState) {
      this.renderer.setAttribute(
        this.defaultMenu.nativeElement,
        'aria-hidden',
        'false'
      );
    }
    this.ref.detectChanges();
  }

  closeHamburger() {
    if (this.sideMenuState.mobileState) {
      this.renderer.setAttribute(
        this.defaultMenu.nativeElement,
        'aria-hidden',
        'true'
      );
    }
    this.sideMenuState.menuState = false;
    this.analyticsService.navType = 'tNav';
    this.activeSlide = 'defaultMenu';
    this.ref.detectChanges();
  }

  toggleSearchMenu() {
    if (this.activeSlide === 'searchMenu') {
      this.activeSlide = 'defaultMenu';
      this.renderer.setAttribute(
        this.defaultMenu.nativeElement,
        'aria-hidden',
        'false'
      );
    } else {
      this.activeSlide = 'searchMenu';
      if (this.sideMenuState.mobileState) {
        this.sideMenuState.menuState = true;
        this.backButton = true;
        this.renderer.setAttribute(
          this.defaultMenu.nativeElement,
          'aria-hidden',
          'true'
        );
      }
    }
    this.ref.detectChanges();
  }

  closeMenu(){
    this.menuItemData = {...{'close':true}};
    this.ref.detectChanges();
  }


  onUtilClick(event, evtName){
    const callBackEvent = new CustomEvent(evtName, { bubbles: true, cancelable: true, detail: event });
     window.dispatchEvent(callBackEvent);
     this.digitalUnavEvent.emit(evtName)
    if (this.sideMenuState.mobileState) {
      this.closeHamburger();
    }
  }
}
