import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  Renderer2,
  ViewChild,
  OnDestroy,
  PLATFORM_ID,
  Inject
} from '@angular/core';

import { AnalyticsService } from '../../services/analytics.service';
import { SessionService } from '../../services/session.service';
import { LogoutService } from '../../services/logout.service';
import { takeUntil } from 'rxjs/operators';
import { isPlatformBrowser } from '@angular/common';
import { Subject } from 'rxjs';
import {WindowRefService} from "../../services/window-ref.service";
import {Config, UNAV_CONFIG} from '../../config';

@Component({
  selector: 'tmo-log-in-menu',
  templateUrl: './ntm-log-in-menu.component.html',
  styleUrls: ['./ntm-log-in-menu.component.scss']
})
export class NtmLogInMenuComponent implements OnDestroy {
  @Output() public closeLoginMenu = new EventEmitter();
  @Input() data: any;
  @Input() isOpened: boolean;
  @Input() setInputFocus: boolean;
  public ngUnsubscribe: Subject<boolean> = new Subject();
  public isswitchAccount: boolean = this.sesionService.getSessionAttributes().switchAccount === 'true';
  private loginRedirect: boolean = this.config.loginRedirect || false;

  @ViewChild('login') loginElem: ElementRef;
  constructor(
    @Inject(PLATFORM_ID) private platformId: string,
    private readonly sesionService:SessionService,
    public readonly analyticsService: AnalyticsService,
    private readonly logoutService:LogoutService,
    private readonly windowRef: WindowRefService,
    @Inject(UNAV_CONFIG) private readonly config: Config
  ) {}

  emitClose(event) {
    if (!this.loginElem.nativeElement.contains(event.target)) {
      this.closeLoginMenu.emit(true);
    }
  }
  logout() {
    this.logoutService.doLogout().pipe(takeUntil(this.ngUnsubscribe)).subscribe();
  }
  loginNavigation(url: string,linksTarget: string) {
    const window = this.windowRef.nativeWindow();
    let dynamicUrl = url;
    const target = linksTarget === 'NEW' ? '_blank' : '_self';
    if(this.loginRedirect) {
      const redirectUrl = window.location.href;
      const parsedUrl = this.logoutService.getParsedUrlForAuthUNAV(redirectUrl, true);
      dynamicUrl = dynamicUrl + '?dest=' + parsedUrl;
    }
    window.open(dynamicUrl,
      target);
  }
  public ngOnDestroy() {
    if (!isPlatformBrowser(this.platformId)) return;
    this.ngUnsubscribe.next(true);
    this.ngUnsubscribe.complete();
  }
}
