import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { UNAV_CONFIG, Config } from '../config';
import { LinkUrl, IUserSession } from '../models';
import { UNAV_USER_STATE } from '../util/user-state';
import { map, share } from 'rxjs/operators';
import { CookieService } from './cookie.service';


@Injectable({
  providedIn: 'root'
})

export class SessionService {
  private sessionHost = this.config.sessionHost || 'https://my.t-mobile.com';
  private unavCookie = 'tmoUnav';
  public userSession: IUserSession = {
    switchAccount: "flase",
  };
  private userState$: Observable<LinkUrl.CustomerTypeEnum> = this.http.jsonp(`${this.sessionHost}/bin/corsdomain`, 'callback')
    .pipe(map((res: IUserSession) => {
      if (res) {
        this.userSession = res;
        return LinkUrl.CustomerTypeEnum.TMOCURRENTLYAUTHENTICATED;
      } else {
        return LinkUrl.CustomerTypeEnum.PROSPECT;
      }
    }, (error) => {
      return LinkUrl.CustomerTypeEnum.PROSPECT;
    }), share());

  constructor(
    @Inject(UNAV_USER_STATE) private readonly userState: LinkUrl.CustomerTypeEnum,
    @Inject(UNAV_CONFIG) private readonly config: Config,
    private readonly cookieService: CookieService,
    private readonly http: HttpClient) { }

  public getState(): Observable<LinkUrl.CustomerTypeEnum> {
    if (this.userState) {
      this.getSessionAttributes();
      return of(this.userState);
    } else {
      return this.getMytmoUserState();
    }
  }
  public getMytmoUserState(): Observable<LinkUrl.CustomerTypeEnum> {
    if (this.istmoDomain()) {
      return this.mytmocookieState();
    } else {
        return this.userState$;
    }
  }

  private mytmocookieState() {
    const mytmoCookie = this.cookieService.getCookie(this.unavCookie);
    try {
      if (mytmoCookie) {
        this.userSession = JSON.parse(mytmoCookie);
        return of(LinkUrl.CustomerTypeEnum.TMOCURRENTLYAUTHENTICATED)
      } else {
        return of(LinkUrl.CustomerTypeEnum.PROSPECT)
      }
    } catch{
      return of(LinkUrl.CustomerTypeEnum.PROSPECT)
    }
  }

  public istmoDomain(): boolean {
    const host = window.location.hostname;
    const istmo = host.endsWith('.t-mobile.com')
    return istmo;

  }
  public getSessionAttributes(): IUserSession {
    const mytmoCookie = this.cookieService.getCookie(this.unavCookie);
    try {
      if (mytmoCookie) {
        this.userSession = JSON.parse(mytmoCookie);
      }
    } catch{
      return this.userSession
    }
    return this.userSession

  }
}