import { Inject, Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';

import { Config, UNAV_CONFIG } from '../config';
import { CookieService } from './cookie.service';


@Injectable({
  providedIn: 'root'
})
export class CartService {

  private cartCookie = this.config.cartCookie;

  constructor(
    private readonly cookieService: CookieService,
    @Inject(UNAV_CONFIG) private readonly config: Config
  ) { }

  public getCartcounter(): Observable<number | undefined> {
    const cookie = this.cartCookie && this.cookieService.getCookie(this.cartCookie);
    if (cookie) {
      try {
        const { cartCounter } = JSON.parse(cookie);
        return of(cartCounter);
      } catch {
        console.log('error parsing cart cookie')
      }
    }
    return of(undefined);
  }
}
