import { NgModule, ModuleWithProviders} from '@angular/core';
import { CommonModule} from '@angular/common';
import { HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';
import { DigitalUnavHeaderComponent } from './components/digital-unav-header/digital-unav-header.component';
import { DigitalUnavFooterComponent } from './components/digital-unav-footer/digital-unav-footer.component';
import { FormsModule } from '@angular/forms';
import { AnalyticsClickDirective } from './directives/analytics-click.directive';
import { NtmUnavDropdownComponent } from "./components/ntm-unav-dropdown/ntm-unav-dropdown.component";
import { NtmLogInMenuComponent } from "./components/ntm-log-in-menu/ntm-log-in-menu.component";
import { NtmSearchBarComponent } from "./components/ntm-search-bar/ntm-search-bar.component";
import { NtmUniversalMenuComponent } from "./components/ntm-universal-menu/ntm-universal-menu.component";
import { FocusListenDirective } from "./directives/focus-out.directive";
import { FooterPanelComponent } from './components/footer-panel/footer-panel.component';
import { UNAV_CONFIG, Config } from './config';
import { NtmUnavDropdownContainerComponent } from './components/ntm-unav-dropdown-container/ntm-unav-dropdown-container.component';
import {MatBadgeModule,MatButtonModule} from '@angular/material';
import { DropdownDirective } from './directives/dropdown.directive';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule,
    HttpClientJsonpModule,
    MatBadgeModule,
    MatButtonModule
  ],
  declarations: [
    DigitalUnavHeaderComponent,
    DigitalUnavFooterComponent,
    NtmUnavDropdownComponent,
    NtmUnavDropdownContainerComponent,
    NtmLogInMenuComponent,
    NtmSearchBarComponent,
    NtmUniversalMenuComponent,
    FocusListenDirective,
    AnalyticsClickDirective,
    FooterPanelComponent,
    DropdownDirective
  ],
  exports: [DigitalUnavHeaderComponent, DigitalUnavFooterComponent],
  entryComponents: [DigitalUnavHeaderComponent, DigitalUnavFooterComponent]
})
export class SharedDigitalUnavModule {

  static forRoot(config: Config): ModuleWithProviders {
    return {
      ngModule: SharedDigitalUnavModule,
      providers: [{provide: UNAV_CONFIG, useValue: config}]
    };
  }

}
