import {
  Component,
  Input,
  forwardRef,
  ChangeDetectorRef,
  Output,
  EventEmitter,
  OnChanges,
  ElementRef,
  HostListener,
} from '@angular/core';
import {
  trigger,
  style,
  state,
  animate,
  transition,
  group,
  query,
  stagger
} from '@angular/animations';
import { DropdownDirective } from '../../directives/dropdown.directive';
import { AnalyticsService } from '../../services/analytics.service';
import { UnavMenuItem } from '../../models';
@Component({
  selector: 'tmo-unav-dropdown',
  templateUrl: './ntm-unav-dropdown.component.html',
  styleUrls: ['./ntm-unav-dropdown.component.scss'],
  providers: [
    {
      provide: DropdownDirective,
      useExisting: forwardRef(() => NtmUnavDropdownComponent)
    }
  ],
  animations: [
    trigger('centerX', [
      state(
        'open',
        style({
          height: '*',
          opacity: 1
        })
      ),
      state(
        'closed',
        style({
          height: '0',
          opacity: 0,
          visibility: 'hidden'
        })
      ),

      transition('closed => open', [
        group([
          animate('.3s cubic-bezier(0.25, 0.1, 0.25, 1)'),
          query('.dropdown__item, p, a', [
            style({ opacity: 0, transform: 'translate3d(-10px, 0, 0)' }),
            stagger(
              '50ms',
              animate(
                '300ms 200ms cubic-bezier(0.25, 0.1, 0.25, 1)',
                style({ opacity: 1, transform: 'translate3d(0, 0px, 0)' })
              )
            )
          ])
        ])
      ]),
      transition('open => closed', [
        group([
          query('.dropdown__item, p, a', [
            animate(
              '300ms cubic-bezier(0.25, 0.1, 0.25, 1)',
              style({ opacity: 0, transform: 'translate3d(-15px, 0, 0)' })
            )
          ]),
          query(':self', [
            animate('300ms 200ms cubic-bezier(0.25, 0.1, 0.25, 1)')
          ])
        ])
      ])
    ])
  ]
})
export class NtmUnavDropdownComponent extends DropdownDirective implements OnChanges{
  public activeIndex = 0;
  public animateState: boolean;
  private clicks = 0;
  @Input() data: any;
  @Input() mobileView: boolean;
  @Input() index: number;
  @Input() menuItemData: UnavMenuItem;
  @Output() closeMenu = new EventEmitter();

  @HostListener('window:click', ['$event'])
  onWindowClick(event): void {
     if (!this.elementRef.nativeElement.contains(event.target) && !this.mobileView) {
      this.menuOpened = false;
      this.changeRef.detectChanges();
     }
  }

  constructor(
    public readonly analyticsService: AnalyticsService,
    ref: ChangeDetectorRef,
    private elementRef: ElementRef,
  ) {
    super(ref);
  }

  ngOnChanges(){
    this.menuOpened = this.menuItemData && !this.menuItemData.close;
  }

  toggleMobileDropdown() {
    const toggleState = !this.menuOpened
    this.menuOpened = toggleState;
    this.changeRef.detectChanges()
  }

  toggleDropdown(e) {
    if (e.type === 'mouseenter' && !this.mobileView) {
      this.menuOpened = true;
      this.changeRef.detectChanges();
      return;
    }
    if (e.type === 'mouseleave' && !this.mobileView) {
      this.menuOpened = false;
      this.changeRef.detectChanges();
      return;
    }
  }

  touchToggleDropdown(e: Event, items: number) {
    const srcId: string = e.target ? (e.target as Element).id : '';
    const parentEle = e.target  ? (e.target as Element)['parentElement'] : '';
    const parentSrcId: string = parentEle  ? parentEle['id'] : '';
    const isnavLink = srcId.startsWith('digital-header-nav-link-head') || parentSrcId.startsWith('digital-header-nav-link-head')
    /*if (!this.mobileView && isnavLink && items > 0) {
     
      if(!this.menuOpened){
        e.preventDefault();
        this.closeMenu.emit();
      }
      setTimeout(() => {
      const IS_OPENED = !this.menuOpened;
          this.menuOpened = IS_OPENED;
          this.changeRef.detectChanges();
        },0);
    }*/

    if (e.type === 'touchend' && !this.mobileView) {
      if(!this.menuOpened)
      {
        e.preventDefault();
        this.menuOpened = true;
        this.changeRef.detectChanges();
        return;
      }
      else {
        this.menuOpened = false;
        this.changeRef.detectChanges();
        this.closeMenu.emit();
        return;
      }
    }

  }
}
