
import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { UNAV_CONFIG, Config } from '../config';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import {WindowRefService} from "./window-ref.service";
export interface LogoutService {
    doLogout(): any;
    getParsedUrlForAuthUNAV(url: string, isLogin: boolean): string;
}
@Injectable({
    providedIn: 'root'
})
export class LogoutService {
    private iamHost: string = this.config.iamHost || 'https://account.t-mobile.com';
    private sessionHost: string = this.config.sessionHost || 'https://my.t-mobile.com';
    private logoutRedirect: boolean = this.config.logoutRedirect || false;

    constructor(
        private readonly http: HttpClient,
        @Inject(UNAV_CONFIG) private readonly config: Config,
        private readonly windowRef: WindowRefService) { }

    public doLogout(): Observable<boolean> {
        return this.http.jsonp(`${this.iamHost}/oauth2/v1/signout`, 'callback').pipe(map((res) => {
            if (res) {
                this.mytmoLogout();
                return true;
            } else {
                return false;
            }
        }
        ));
    }
    private mytmoLogout() {
        if (sessionStorage) {
            sessionStorage.clear();
        }
        if (document.forms['MyTmosessionLogOut']) {
            document.forms['MyTmosessionLogOut'].action = `${this.sessionHost}/apps/mytmobile/eservice/servlet/Logout`;
            const hiddenRedirectUrl = document.getElementById("redirectUrlParamText");
            if(hiddenRedirectUrl) {
              if(this.logoutRedirect) {
                const window = this.windowRef.nativeWindow();
                const filteredUrl = this.getParsedUrlForAuthUNAV(window.location.href, false);
                hiddenRedirectUrl.setAttribute('value', filteredUrl);
              } else if(hiddenRedirectUrl.parentNode) {
                hiddenRedirectUrl.parentNode.removeChild(hiddenRedirectUrl);
              }
            }
            document.forms['MyTmosessionLogOut'].submit();
        }
    }

    public getParsedUrlForAuthUNAV(url: string, isLogin: boolean): string {
      const parsedUrl = new URL(url);
      let filteredUrl =  parsedUrl.origin + parsedUrl.pathname;
      filteredUrl = (parsedUrl.pathname === '/search' && isLogin) ? (filteredUrl + '?unav=mytmo'): filteredUrl;
      return filteredUrl;
    }
}
