import { isPlatformBrowser, JsonPipe } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map, share } from 'rxjs/operators';

import { Config, UNAV_CONFIG } from '../config';
import { UnavComponent, IUserSession } from '../models';
import { WindowRefService } from './window-ref.service';
import { SessionService } from './session.service';
import { CookieService } from './cookie.service';

export interface ISites {
  sites?: Array<string>;
  groups?: Array<IGropus>;
}
export interface IGropus {
  id?: string;
  path: Array<string>;
}

@Injectable({
  providedIn: 'root'
})
export class ContentPathService {
  private baseUrl = this.config.baseUrl;
  private contentMap$: Observable<ISites> = this.http
    .get<ISites>(`${this.baseUrl}/content/sites.json`)
    .pipe(share());
  constructor(
    private readonly http: HttpClient,
    @Inject(UNAV_CONFIG) private readonly config: Config,
    @Inject(PLATFORM_ID) private platformId: string,
    private readonly windowRef: WindowRefService,
    private readonly cookieService: CookieService,
    private readonly sessionService: SessionService


  ) {}

  public resolveContentUrl(
    component: UnavComponent.ComponentTypeEnum,
    clientId?: string,
    site?: string
  ): Observable<string> {
    switch (site) {
      case UnavComponent.ComponentNavTypeEnum.PROSPECT:
        return this.getContentMap().pipe(
          map(val => this.getUrlPath(val)),
          map(id => `${this.baseUrl}/content/${id}.${component}.json`)
        );
      case UnavComponent.ComponentNavTypeEnum.CUSTOMER:
        return this.getAuthContent(component);
      default:
        return of(`${this.baseUrl}/content/${clientId}.${component}.json`);
    }

  }

  public getContentMap(): Observable<ISites> {
    return this.contentMap$;
  }

  private getUrlPath(res?: ISites): string | undefined {
    if (isPlatformBrowser(this.platformId)) {
      if (this.isB2b()) {
        return 'business';
      }
      let urlpath: string | undefined =
        UnavComponent.ComponentNavTypeEnum.DEFAULT;
      const window = this.windowRef.nativeWindow();
      const curentPath: string = window.location.pathname;
      if (curentPath === '/') return urlpath;
      if (res && res.sites) {
        res.sites.forEach(x => {
          const temp = curentPath.split('/')[1];
          if (x === temp) {
            urlpath = x;
          }
        });
      }
      if (res && res.groups) {
        res.groups.forEach(group => {
          if (group.path) {
            group.path.forEach(path => {
              if (curentPath.startsWith(path)) {
                urlpath = group.id;
              }
            });
          }
        });
      }
      return urlpath;
    }
  }
  private isB2b() {
    const window = this.windowRef.nativeWindow();
    const location: string = window.location.host;
    return location.includes('business.t-mobile.com') ? true : false;
  }
  private getAuthContent(component?: UnavComponent.ComponentTypeEnum) {
    return this.sessionService.getMytmoUserState().pipe(map(() => {
      const state: IUserSession = this.sessionService.userSession;
      let varient = UnavComponent.ComponentNavTypeEnum.MYTMO as string;
      if (state.varient) {
        varient = state.varient ;
      }
      return `${this.baseUrl}/content/${varient}.${component}.json`
    }));
  }
}
