import { Component, QueryList, ContentChildren, AfterContentInit, ChangeDetectorRef } from '@angular/core';
import { DropdownDirective } from '../../directives/dropdown.directive';

@Component({
  selector: 'tmo-unav-dropdown-container',
  template: '<ng-content></ng-content>',
  styles: ['display: inherit']

})
export class NtmUnavDropdownContainerComponent implements AfterContentInit {
  @ContentChildren(DropdownDirective) dropdowns: QueryList<DropdownDirective>;

  constructor(private ref:ChangeDetectorRef) { }

  ngAfterContentInit() {
    // Open the first panel
    // this.dropdowns.toArray()[0].menuOpened = true;
    // Loop through all dropdowns
    this.dropdowns.toArray().forEach((dropdown: DropdownDirective) => {
      // subscribe panel toggle event <
      dropdown.toggle.subscribe((event) => {
        // Open the panel
        this.openPanel(dropdown);
      });
    });
  }

  openPanel(dropdown: DropdownDirective) {
    // close all dropdowns
    this.dropdowns.toArray().forEach(p => p.menuOpened = false);
    // open the selected panel
    dropdown.menuOpened = true;
    this.ref.detectChanges();
  }

}
