import { Injectable, Inject, PLATFORM_ID } from '@angular/core';

import { ChildLinkInner, Link, UnavComponent, IUserSession } from '../models';
import { Header } from '../models/header';
import { LinkUrl } from '../models/linkUrl';
import { isPlatformBrowser } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class ManageHeader {
  private headerdata: Header;
  public currentPageUrl = isPlatformBrowser(this.platformId) ? window.location.pathname.toLowerCase() : '';
  constructor(@Inject(PLATFORM_ID) private platformId: string,) { }
  public getHeaderData(data: Header, filterBy: LinkUrl.CustomerTypeEnum, userSession?: IUserSession ,siteType?:string): Header {
    this.headerdata = new Header();
    this.headerdata.sitesTitle = data.sitesTitle;
    this.headerdata.title = data.title;
    this.headerdata.search = data && data.search ? data.search : {};
    if (data.logo) {
      const { id,targetWindow,label,description,icon,hoverIcon,linkType} = data.logo;
      this.headerdata.logo = { id, targetWindow, label, description, icon, hoverIcon, linkType };
      if (data.logo.linkUrl) {
        const linkUrls = data.logo.linkUrl;
        for (const entry of linkUrls) {
          if ((entry.customerType || []).includes(filterBy)) {
            const linkUrlResp: Array<LinkUrl> = [];
            linkUrlResp.push({
              linkText: entry.linkText,
              linkHref: entry.linkHref
            });
            this.headerdata.logo.linkUrl = linkUrlResp;
            break;
          }
        }
      }
    }

    if (data.sites) {
      for (const site of data.sites) {
        const linkUrls = site.linkUrl || [];
        for (const entry of linkUrls) {
          if ((entry.customerType || []).includes(filterBy)) {
            const linkUrlResp: Array<LinkUrl> = [];
            linkUrlResp.push({
              linkText: entry.linkText,
              linkHref: entry.linkHref
            });
            this.headerdata.sites = this.headerdata.sites || [];
            this.headerdata.sites.push({
              id: site.id,
              targetWindow: site.targetWindow,
              label: site.label,
              description: site.description,
              icon: site.icon,
              hoverIcon: site.hoverIcon,
              linkType: site.linkType,
              linkUrl: linkUrlResp,
              activeState: site.activeState
            });
            break;
          }
        }
      }
    }

    if (data.navigationLinks) {
      for (const navigationLink of data.navigationLinks) {
        const linkUrls = navigationLink.linkUrl || [];
        for (const entry of linkUrls) {
          if ((entry.customerType || []).includes(filterBy)) {
            if(isPlatformBrowser(this.platformId) && !navigationLink['active']) navigationLink['active'] = this.checkForActiveNavigationLink(entry.linkHref); //platform browser check for ssr
            const linkUrlRespObj = this.getLinkUrlResponse(entry, filterBy, navigationLink['active'])
            this.headerdata.navigationLinks = this.headerdata.navigationLinks || [];
            this.headerdata.navigationLinks.push({
              id: navigationLink.id,
              targetWindow: navigationLink.targetWindow,
              label: navigationLink.label,
              description: navigationLink.description,
              icon: navigationLink.icon,
              hoverIcon: navigationLink.hoverIcon,
              linkType: navigationLink.linkType,
              linkUrl: linkUrlRespObj.linkUrlResp,
              active: navigationLink.active || linkUrlRespObj.activeLink
            });
            break;
          }
        }
      }
    }

    if (data.utility) {
      for (const util of data.utility) {
        const linkUrls = util.linkUrl || [];
         for (const entry of linkUrls) {
          if ((entry.customerType || []).includes(filterBy)) {
            if(isPlatformBrowser(this.platformId) && !util['active']) util['active'] = this.checkForActiveNavigationLink(entry.linkHref);
            const linkUrlRespArr =  this.getLinkUrlResponse(entry, filterBy, util['active'])
            this.headerdata.utility = this.headerdata.utility || [];
            this.headerdata.utility.push({
              id: util.id,
              targetWindow: util.targetWindow,
              label: util.label,
              description: util.description,
              icon: util.icon,
              hoverIcon: util.hoverIcon,
              linkType: util.linkType,
              linkUrl: linkUrlRespArr.linkUrlResp,
              showInMobileView: util.showInMobileView,
              buttonType:util.buttonType,
              disabled:util.disabled,
              active: util['active'] || linkUrlRespArr.activeLink
            });
            break;
          }
        }
      }
    }

    if (data.userLinks) {
      const { labelColor, mobileIcon } = data.userLinks;
      let { labelText } = data.userLinks;
      if (siteType === UnavComponent.ComponentNavTypeEnum.CUSTOMER) {
        const name  = userSession && userSession.name;
        if (name) {
          labelText = name.substring(0, 20);
        }
      }

      this.headerdata.userLinks = { labelColor, labelText, mobileIcon };
      if (data.userLinks.link) {
        const userLink = data.userLinks.link;
        const userLinkUrlResp: Array<Link> = [];
        for (const entryLink of userLink) {
          if (
            entryLink.linkUrl &&
            entryLink.linkUrl[0] &&
            (entryLink.linkUrl[0].customerType || []).includes(filterBy)
          ) {
            const childUrlResp: Array<ChildLinkInner> = [];
            for (const childEntry of entryLink.linkUrl[0].childLinks || []) {
              const childLinkRep: Array<Link> = [];
              for (const childLinkEntry of childEntry.categorizedlink || []) {
                const childLinkUrlResp: Array<LinkUrl> = [];
                if (
                  childLinkEntry.linkUrl &&
                  childLinkEntry.linkUrl[0] &&
                  (childLinkEntry.linkUrl[0].customerType || []).includes(
                    filterBy
                  )
                ) {
                  childLinkUrlResp.push({
                    linkText: childLinkEntry.linkUrl[0].linkText,
                    linkHref: childLinkEntry.linkUrl[0].linkHref
                  });
                  childLinkRep.push({
                    linkUrl: childLinkUrlResp,
                    id: childLinkEntry.id,
                    targetWindow: childLinkEntry.targetWindow,
                    label: childLinkEntry.label,
                    description: childLinkEntry.description,
                    icon: childLinkEntry.icon,
                    hoverIcon: childLinkEntry.hoverIcon,
                    linkType: childLinkEntry.linkType
                  });
                }
              }
              if(childLinkRep.length > 0){
                childUrlResp.push({
                  categoryName: childEntry.categoryName,
                  categorizedlink: childLinkRep
                });
              }
            } 
            const linkUrlResp: Array<LinkUrl> = [];
            if(childUrlResp.length > 0){
              linkUrlResp.push({
                linkText: entryLink.linkUrl[0].linkText,
                linkHref: entryLink.linkUrl[0].linkHref,
                childLinks: childUrlResp
              });
            }
            if(linkUrlResp.length > 0){
              userLinkUrlResp.push({
                id: entryLink.id,
                targetWindow: entryLink.targetWindow,
                label: entryLink.label,
                description: entryLink.description,
                icon: entryLink.icon,
                hoverIcon: entryLink.hoverIcon,
                linkType: entryLink.linkType,
                linkUrl: linkUrlResp
              });
            }
          }
        }
        this.headerdata.userLinks.link = userLinkUrlResp;
      }
    }
    return this.headerdata;
  }
 public getLinkUrl(url){
    url = url.indexOf('http') === 0 ? new URL(url).pathname.toLowerCase() 
          : url.indexOf('www.') === 0 ? new URL('https://' + url).pathname.toLowerCase() 
          : url.split(/[?#]/)[0];
    return url;
  }

  public checkForActiveNavigationLink(url){
    const linkURL = url ? this.getLinkUrl(url.toLowerCase()) : url;
    return this.currentPageUrl === linkURL
  }

  public getLinkUrlResponse(entry, filterBy, activeLink){
    const childUrlResp: Array<ChildLinkInner> = [];
    const linkUrlResp: Array<LinkUrl> = [];
    linkUrlResp['active'] = activeLink
    for (const childEntry of entry.childLinks || []) {
      const childLinkRep: Array<Link> = [];
      if (childEntry.categorizedlink) {
        for (const childLinkEntry of childEntry.categorizedlink) {
          if(isPlatformBrowser(this.platformId) && !activeLink) activeLink = this.checkForActiveNavigationLink(childLinkEntry.linkUrl[0].linkHref);
          const childLinkUrlResp: Array<LinkUrl> = [];
          if (
            childLinkEntry.linkUrl &&
            childLinkEntry.linkUrl[0] &&
            childLinkEntry.linkUrl[0].customerType &&
            childLinkEntry.linkUrl[0].customerType.includes(filterBy)
          ) {
            childLinkUrlResp.push({
              linkText: childLinkEntry.linkUrl[0].linkText,
              linkHref: childLinkEntry.linkUrl[0].linkHref
            });
            childLinkRep.push({
              linkUrl: childLinkUrlResp,
              id: childLinkEntry.id,
              targetWindow: childLinkEntry.targetWindow,
              label: childLinkEntry.label,
              description: childLinkEntry.description,
              icon: childLinkEntry.icon,
              hoverIcon: childLinkEntry.hoverIcon
            });
          }
        }
      }
      childUrlResp.push({
        categoryName: childEntry.categoryName,
        categorizedlink: childLinkRep
      });
    }
    
    linkUrlResp.push({
      linkText: entry.linkText,
      linkHref: entry.linkHref,
      childLinks: childUrlResp,
    });
    return {  linkUrlResp, activeLink };
  }
}
