import { Component, Input, ChangeDetectionStrategy, forwardRef, ChangeDetectorRef, OnChanges } from '@angular/core';
import { DropdownDirective } from '../../directives/dropdown.directive';
import {
  trigger,
  style,
  state,
  animate,
  transition,
  group,
  query,
  stagger
} from '@angular/animations';
@Component({
  selector: 'tmo-footer-panel',
  templateUrl: './footer-panel.component.html',
  styleUrls: ['./footer-panel.component.scss'],
  providers: [{ provide: DropdownDirective, useExisting: forwardRef(() => FooterPanelComponent) }],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('centerX', [
      state('open', style({
        height: '*', opacity: 1
      })),
      state('closed', style({
        height: '0', opacity: 0, visibility: 'hidden'

      })),

      transition('closed => open', [
        group([
          animate('.3s cubic-bezier(0.25, 0.1, 0.25, 1)'),
          query(
          '.panel__item',
          [
            style({ opacity: 0, transform: 'translate3d(-10px, 0, 0)'}),
            stagger(
              '50ms',
              animate(
                '300ms 200ms cubic-bezier(0.25, 0.1, 0.25, 1)',
                style({ opacity: 1, transform: 'translate3d(0, 0px, 0)' })
              )
            )
          ])
        ])
      ]),
        transition('open => closed', [
            group(
              [
                query(
                  '.panel__item',
                  [
                    animate(
                      '300ms cubic-bezier(0.25, 0.1, 0.25, 1)',
                      style({ opacity: 0, transform: 'translate3d(-15px, 0, 0)' }),
                    )
                  ]
                ),
                query(':self',[animate('300ms 200ms cubic-bezier(0.25, 0.1, 0.25, 1)')])

              ]
            )

        ])
    ])
  ],
})
export class FooterPanelComponent extends DropdownDirective  {
  @Input() public content: any;
  @Input() public index: number;

  public menuOpened:boolean;
  constructor(ref:ChangeDetectorRef) {
    super(ref)
  }
 
}


