import { Directive, Input, OnChanges, SimpleChanges, Renderer2, ElementRef, OnDestroy, Output, EventEmitter } from '@angular/core';

@Directive({
  selector: '[tmoFocusListen]'
})
export class FocusListenDirective implements OnChanges, OnDestroy {
  @Input('tmoFocusListen') isFocused: boolean;
  public listenerFn: () => void;

  constructor(public renderer: Renderer2, public eRef: ElementRef) { }
  @Output() focusChange = new EventEmitter();

  // Fire event to parent navigation component when user clicks outside of element
  // This will be used to close out of the component view
  ngOnChanges(changes: SimpleChanges) {
    if (this.isFocused) {
      setTimeout(() => {
        this.listenerFn = this.renderer.listen('document', 'click', (event) => {
          if (!this.eRef.nativeElement.contains(event.target)) {
            this.focusChange.emit(true)
          }
        })
      }, 0);
    }

    else if (this.listenerFn) {
      this.listenerFn();
    }
  }

  ngOnDestroy() {
    if (this.listenerFn) {
      this.listenerFn();
    }
  }
}
