import { BrowserModule, BrowserTransferStateModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {SharedDigitalUnavModule, UNAV_USER_STATE, LinkUrl} from '@tmo/shared/digital-unav'
import { environment } from '../environments/environment';

@NgModule({
  declarations: [AppComponent],
  imports: [BrowserModule.withServerTransition({ appId: 'serverApp' }),
    BrowserAnimationsModule, 
    BrowserTransferStateModule,
    SharedDigitalUnavModule.forRoot({
      baseUrl: environment.unavBaseUrl,
      cartApiUrl: environment.cartApiUrl,
      cartCookie:environment.cartCookie,
      sessionHost :environment.sessionHost
    })],

  providers: [
    { provide: UNAV_USER_STATE, useValue: LinkUrl.CustomerTypeEnum.PROSPECT },
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}
